@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=JetBrains+Mono:wght@300;400;500;600;700&display=swap');

body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: "kern" 1, "liga" 1, "calt" 1;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
  background: #0B1426;
  color: #FFFFFF;
}

/* Neural network themed scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(11, 20, 38, 0.8);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #00D4FF 0%, #00FF88 100%);
  border-radius: 4px;
  transition: all 0.3s ease;
  box-shadow: 0 0 10px rgba(0, 212, 255, 0.3);
}

::-webkit-scrollbar-thumb:hover {
  box-shadow: 0 0 15px rgba(0, 212, 255, 0.5);
}

/* Neural network selection styling */
::selection {
  background: rgba(0, 212, 255, 0.3);
  color: #FFFFFF;
}

::-moz-selection {
  background: rgba(0, 212, 255, 0.3);
  color: #FFFFFF;
}

/* Neural network focus styles */
*:focus-visible {
  outline: 2px solid #00D4FF;
  outline-offset: 2px;
  border-radius: 4px;
  box-shadow: 0 0 10px rgba(0, 212, 255, 0.5);
}

/* Reduce motion for users who prefer it */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  * {
    border-color: currentColor !important;
  }
}

/* Performance optimizations */
* {
  box-sizing: border-box;
}

/* Improve text rendering */
body {
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Neural network animations */
@keyframes neuralPulse {
  0%, 100% {
    opacity: 0.4;
    transform: scale(1);
  }
  50% {
    opacity: 0.7;
    transform: scale(1.02);
  }
}

@keyframes dataFlow {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    transform: translateX(100%);
    opacity: 0;
  }
}

@keyframes connectionGlow {
  0%, 100% {
    box-shadow: 0 0 5px rgba(0, 212, 255, 0.3);
  }
  50% {
    box-shadow: 0 0 20px rgba(0, 212, 255, 0.8);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* GPU acceleration for smooth animations */
.MuiPaper-root,
.MuiFab-root,
.MuiButton-root {
  will-change: transform;
  transform: translateZ(0);
}

/* Subtle neural network effects - simplified */

code {
  font-family: 'JetBrains Mono', source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media print {
  /* Hide non-printable elements */
  .no-print {
    display: none !important;
  }
  
  /* Reset background colors and shadows */
  body {
    background: white !important;
    -webkit-print-color-adjust: exact !important;
    print-color-adjust: exact !important;
  }
  
  /* Ensure proper page breaks */
  .page-break {
    page-break-before: always;
  }
  
  /* Remove unnecessary margins and padding */
  @page {
    margin: 20mm;
  }
  
  /* Ensure content fits properly */
  .MuiContainer-root {
    max-width: 100% !important;
    padding: 0 !important;
  }
  
  /* Adjust typography for print */
  body {
    font-size: 12pt;
  }
  
  /* Ensure links are visible in print */
  a {
    text-decoration: none !important;
  }
  
  /* Hide scroll-to-top button and navigation */
  .MuiBottomNavigation-root,
  .scroll-top-button {
    display: none !important;
  }
}